import React from "react";
import Router from "next/router";
import fetch from "isomorphic-unfetch";
import Helmet from "react-helmet";

function Blogpage(props) {

  console.log("props", props);
  const {title} = props['jsonData'];

  return (
    <div>
      <Helmet
        title={title}
        meta={[
          { name: "author", content: "Charan" },

          { name: "twitter:title", content: title },

          { property: "og:title", content: title },
          { property: "og:type", content: "website" },
         
          { property: "og:description", content: title + title },

          { name: "viewport", content: "width=device-width, maximum-scale=1" },
          { name: "apple-itunes-app", content: "app-id=1125423676" }
        ]}
      />
      <p>{JSON.stringify(props['jsonData'])}</p>
    </div>
  );
}

Blogpage.getInitialProps = async ctx => {
  console.log("router", ctx);

  const res = await fetch(`https://jsonplaceholder.typicode.com/todos/${ctx['asPath'].split('=')[1]}`);
  const json = await res.json();
  return { jsonData: json };
};

export default Blogpage;
